body {
    margin: 0;
    padding: 0;
    font-family: 'Montserrat', sans-serif;
    height: 100vh;
    width: 100%;
    background-color: #35dad2;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
}
.mainButtonWrapper {

    background: none;
    border-radius: 20px;
    padding-left: 0px;
    padding-right: 0px;
}
.mainButton {
    font-weight: 500;
    background-color: white;
    border: 2px solid rgb(0, 0, 0);
    border-radius: 20px;
    padding-left: 10px;
    padding-right: 10px;

}